global.browser = require('webextension-polyfill')

/**
 * message = {
 *   action: 'handler_function_name',
 *   data: {},
 *   from: '',
 *   to: '',
 *   tabId: 8,
 *   middleWare: 'middle_ware_function_name',
 * }
 */

export let ScriptType = {
  POPUP: 0,
  CONTENT_UI: 1,
  CONTENT_DATA: 2,
  CONTENT: 3,
  BACKGROUND: 4
};

export class Messenger {
  constructor(type, eventHandlerList){
    this.__port__ = null;
    this.__event_handler_list__ = eventHandlerList;
    this.__current_type__ = type;
    switch (type) {
      case ScriptType.BACKGROUND:
        this.__init_bg__();
        break;
      case ScriptType.POPUP:
        this.__init_popup__();
        break;
      case ScriptType.CONTENT:
        this.__init_content__();
        break;
      case ScriptType.CONTENT_DATA:
        this.__init_data__();
        break;
      case ScriptType.CONTENT_UI:
        this.__init_ui__();
        break;
      default:
        return new Error('wrong script type');
    }
  }

  __start_listener__(){
    // console.log('starting listener', this.__current_type__);
    browser.runtime.onMessage.addListener(message => this.__reg_listener__(message));
  }

  __start_listener_on_port__(){
    console.log('starting listener on port', this.__current_type__);
    //  port  port 
    if (this.__port__){
      this.__port__.onMessage.addListener(message => this.__reg_listener__(message));
    }
  }

  __reg_listener__(message){
    console.log('registering listeners', this.__current_type__);
    let eventList = this.__event_handler_list__;
    function handle (message) {
      if (message.action in eventList) {
        eventList[message.action](message);
      } else {
        console.log('DEBUG INFO', 'no corresponding message action handler');
      }
    }
    switch (this.__current_type__) {
      case ScriptType.BACKGROUND:
        this.__bg_middle_ware__(message).then(rst => this.__bg_router__(rst))
          .then(rst => {
            if (rst === true) {
              console.log('DEBUG INFO', 'message routed to', message.to);
            } else {
              handle(rst)
            }
          }).catch(err => {console.log('DEBUG INFO', err)});
        break;
      case ScriptType.CONTENT_UI:
      case ScriptType.CONTENT_DATA:
      case ScriptType.CONTENT:
      case ScriptType.POPUP:
        handle(message);
    }
  }

  /**
   *  middleWare field
   * @param message 
   * @private
   */
  async __bg_middle_ware__(message){
    console.log('entering middleware handle',this.__current_type__ , message);
    if (
      'middleWare' in message &&
      message.middleWare in this.__event_handler_list__
    ){
      /*
       *  message 
       *  data field,  data,  message 
       * TODO:  to / tabId  message 
       * TODO:  data field,  data field
       */
      let rst = await this.__event_handler_list__[message.middleWare](message);
      Object.assign(message, rst);
      return rst;
    }
    return message;
  }

  /**
   * background page  UI <--> DATA 
   * @param message 
   * @private
   */
  async __bg_router__(message){
    console.log('routing message', this.__current_type__, message);
    if (
      !message.hasOwnProperty('to') ||
      !message.hasOwnProperty('from') ||
      message.to === message.from ||
      message.to === ScriptType.BACKGROUND
    ){
      return message;
    }
    let msgSent = {};
    switch (message.to) {
      case ScriptType.CONTENT_UI:
        msgSent = {
          action: message.action,
          data: message.data,
          from: message.from,
          tabId: message.tabId
        };
        browser.tabs.sendMessage(msgSent.tabId, msgSent).then().catch();
        break;
      case ScriptType.CONTENT_DATA:
        msgSent = {
          action: message.action,
          data: message.data,
          from: message.from,
        };
        if ('tabId' in message){
          msgSent.tabId = message.tabId;
        }
        this.__port__.postMessage(msgSent);
        break;
      case ScriptType.CONTENT:
        //  content script  content UI 
        msgSent = {
          action: message.action,
          data: message.data,
          from: message.from,
        };
        if ('tabId' in message){
          msgSent.tabId = message.tabId;
        }
        browser.tabs.sendMessage(msgSent.tabId, msgSent).then().catch();
        break;
      case ScriptType.POPUP:
        // TODO:  POPUP 
        break;
      default:
        break;
    }
    return true;
  }

  /************************************************ INIT FUNCTIONS ****************************************************/

  __init_popup__(){
    this.__start_listener__()
  }

  __init_ui__(){
    this.__start_listener__()
  }

  __init_content__(){
    this.__start_listener__()
  }

  __init_bg__(){
    browser.runtime.onConnect.addListener(port => {
      //  data  port
      this.__port__ = port;
      this.__start_listener_on_port__();
    });
    this.__start_listener__();
  }

  __init_data__(){
    this.__port__ = browser.runtime.connect();
    this.__start_listener__();
  }

  /******************************************* MESSAGE SENDING FUNCTION ***********************************************/
  async __append_tab_id__(message){
    let tabs = await browser.tabs.query({currentWindow: true, active: true});
    if (tabs.length > 0) {
      message.tabId = tabs[0].id;
    }
    console.log(6666666666, message);
    return message;
  }

  sendToUI(message){
    // 
    message.from = this.__current_type__;
    if (this.__current_type__ === ScriptType.BACKGROUND){
      if ('tabId' in message){
        return browser.tabs.sendMessage(message.tabId, {
          action: message.action,
          data: message.data,
        }).then(() => {return Promise.resolve('success')}).catch();
      } else {
        return browser.tabs.query({active: true, currentWindow: true}).then(tabs => {
          return browser.tabs.sendMessage(tabs[0].id, {
            action: message.action,
            data: message.data,
          });
        })
      }
    } else {
      //  background  bg 
      message.to = ScriptType.CONTENT_UI;
      return browser.runtime.sendMessage(message).then(() => {return Promise.resolve('success')}).catch();
    }
  }

  sendToBg(message){
    message.from = this.__current_type__;
    if (this.__current_type__ === ScriptType.CONTENT_UI){
      return this.__append_tab_id__(message)
        .then(msg => browser.runtime.sendMessage(msg))
    }
    return browser.runtime.sendMessage(message);
  }

  sendToData(message){
    let msgSent = {
      action: message.action,
      data: message.data,
      from: this.__current_type__,
    };
    if (this.__current_type__ === ScriptType.BACKGROUND && this.__port__){
      this.__port__.postMessage(msgSent);
      return Promise.resolve('success');
    }
    msgSent.to = ScriptType.CONTENT_DATA;
    if (this.__current_type__ !== ScriptType.CONTENT_DATA ||
      this.__current_type__ !== ScriptType.BACKGROUND){
      return this.__append_tab_id__(msgSent)
        .then(msg => {
          return browser.runtime.sendMessage(msg).then(() => {
            return Promise.resolve('success')
          })
        })
    }
    return browser.runtime.sendMessage(msgSent).then(() => {
      return Promise.resolve('success')
    })
  }

  sendToContent(message){}

  sendToPopup(message){}
}
