global.md5 = require('blueimp-md5')
import axios from "axios";
import { sha256 } from 'js-sha256';
import { parseResult, platformName} from './config'
import {localConfig} from "../localConfig"

let ipInfo = null

function getIpInfo() {
    axios({
        method: 'get',
        url: 'https://ipinfo.io/json',
        timeout: 3000
    }).then(response => {
        console.log('ipinfo:', response)
        ipInfo = response.data
    }).catch(() => {
        //  
        axios({
            method: 'get',
            url: 'http://ip-api.com/json/'
        }).then(response => {
            console.log('ipinfo:', response)
            ipInfo = response.data
        })
    })
}
getIpInfo()

/**
 * 
 * */
function uploadData(post_id, platform,  content_id, mission_id, post_raw_data, file_type) {
    // console.debug('entering function uploadData');
    let signature = sha256.hmac('XSTWPW8A4FYLSTVG42FP2Y27Y54ECPBE', post_id)
    let data = {
        plugin_id: localConfig.PLUGIN_ID,
        post_id: post_id,
        platform: platform,
        content_id: content_id,
        mission_id: mission_id,
        file_type: file_type,
        signature: signature
    }
    // console.log("data", data);
    chrome.storage.local.get(["api_url"], (result) => {
        let api_url = result.api_url;
        axios({
            url: api_url,
            method: "post",
            data: data
        }).then((response) => {
            if(response.status === 200){
                let put_url = response.data["put_url"];
                if (put_url){
                    axios.put(put_url, JSON.stringify(post_raw_data), {headers: {'content-type': 'application/json'}});
                }
            }
        });
    });
}
function sendMessage(post_id, platform, file_type, mission_id, data) {
    let request_body = {
        post_id: post_id,
        platform: platform,
        file_type: file_type,
        mission_id: mission_id,
        data: JSON.stringify(data),
    };
    chrome.storage.local.get(["api_url"], (result) => {
        axios({
            method: "post",
            url: result.api_url,
            data: request_body,
        });
    });

}
/**
 * item
 * */
function getItem(post_id, format_data, platform, page_id) {

    // 1. 
    let item = {
        post_id: post_id,
        ext_data: format_data
    }
    if (ipInfo) {
        item['city'] = ipInfo.city
        item['country'] = ipInfo.country
        item['ip'] = ipInfo.ip ? ipInfo.ip : ipInfo.query
        item['region'] = ipInfo.regionName ? ipInfo.regionName : ipInfo.region
        item['timezone'] = ipInfo.timezone
    }
    if(platform === platformName.FACEBOOK){
        // facebook page_id
        item["page_id"] = page_id;
    }
    return item;
}
/**
 * gh  
 * */
function execApi(post_id, platform, content_id, mission_id, post_raw_data, file_type) {
    if(localConfig.PLUGIN_ID === 5){
        sendMessage(post_id, platform, file_type, mission_id, post_raw_data);
    }
    else{
        uploadData(post_id, platform, content_id, mission_id, post_raw_data, file_type);
    }

}

/**
 * AD 
 * */
function execAd(post_id, platform, content_id, mission_id, post_raw_data, format_data, page_id) {
    if (format_data === undefined){
        // 
        execApi(post_id, platform, content_id, mission_id, post_raw_data, parseResult.PARSE_ERROR);
    }
    else{
        let item = getItem(post_id, format_data, platform, page_id)
        // 2. oss
        execApi(post_id,platform, content_id, mission_id, item, parseResult.PARSE_SUCCESS)
    }
}

/**
 *  
 * param message 
 * return
 * */
function uploadAd(message) {
    let post_id=message.data.post_id;
    let format_data=message.data.format_data;
    let post_raw_data= message.data.post_raw_data;
    let content_id = md5(post_id);
    let platform = message.data.platform;
    let mission_id = message.data.mission_id;
    let page_id = message.data.hasOwnProperty("page_id") ? message.data.page_id :  "";
    execAd(post_id, platform, content_id, mission_id, post_raw_data, format_data, page_id)

}


/**
 *  
 * :param message 
 * return
 * */
let alreasyUploadsPostIds = new Set()

function uploadAdSet(message) {
    let postIds = message.data.data_list;
    let platform = message.data.platform;
    let mission_id = message.data.mission_id;
    Object.keys(postIds).forEach(post_id => {
        let data = postIds[post_id];
        let content_id = md5(post_id);
        let format_data = data.format_data;
        let post_raw_data = data.post_raw_data;
        let page_id = data.hasOwnProperty("page_id") ? data.page_id : "";
        if (!alreasyUploadsPostIds.has(post_id)) {
            execAd(post_id, platform, content_id, mission_id, post_raw_data, format_data, page_id)
            alreasyUploadsPostIds.add(post_id)
        }
    })

}

export {uploadAd, uploadAdSet}