// facebookheadersx-frame-option
browser.webRequest.onHeadersReceived.addListener(
  function (info) {
    let headers = info.responseHeaders;
    let index = headers.findIndex(
      (x) => x.name.toLowerCase() === "x-frame-options"
    );
    if (index !== -1) {
      headers.splice(index, 1);
    }
    let polifyIndex = headers.findIndex(
      (x) => x.name.toLowerCase() === "content-security-policy"
    );
    if (polifyIndex !== -1) {
      headers.splice(polifyIndex, 1);
    }
    return { responseHeaders: headers };
  },
  {
    urls: ["*://*.facebook.com/*"],
    types: ["sub_frame"],
  },
  [
    "blocking",
    "responseHeaders",
    chrome.webRequest.OnHeadersReceivedOptions.EXTRA_HEADERS,
  ].filter(Boolean)
);
